import React, { useContext, useEffect, useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import { Avatar, Chip, IconButton, Paper, Typography } from "@material-ui/core";
import PeopleIcon from "@material-ui/icons/People";
import { UserContext } from "../../config/UserContext";

const Profile = () => {
  const mockPostData = ["Projeto1", "Projeto2", "Projeto3", "Projeto4"];

  //const [users, setUsers] = useState([]);

  // useEffect(() => {
  //   firestore.collection('users').get().then((snapshot) => {
  //     if(snapshot){
  //       let data = [];
  //       snapshot.forEach((item) => data.push({...item.data() }));
  //       setUsers(data);
  //     }
  //   })
  // }, []);


  const currentUser = useContext(UserContext);

  const handleClick = () => {
    console.info("You clicked the Chip.");
  };

  const classes = useStyles();
  return (
    <div className={classes.root}>
      <Paper className={classes.paper}>
        <div className={classes.avatarDiv}>
          <Avatar
            alt="Remy Sharp"
            src={currentUser && currentUser.photoURL}
            className={classes.avatar}
          />
        </div>
        <Typography variant="h6" color="textSecondary" component="p">
          {currentUser && currentUser.displayName}
        </Typography>
        <Typography variant="caption" color="textSecondary" component="p">
          {currentUser && currentUser.email}
        </Typography>

        <Typography variant="h6" color="textSecondary" component="p">
          <IconButton className={classes.icon} aria-label="add to favorites">
            <PeopleIcon />
          </IconButton>
          Alice Lara
        </Typography>
        {/* <div className={classes.chipList}>
          {mockPostData.map((data) => {
            return (
              <Chip
                key={data}
                className={classes.chip}
                size="small"
                label={"#" + data}
                onClick={handleClick}
              />
            );
          })}
        </div> */}
      </Paper>
    </div>
  );
};

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    overflow: "hidden",
    padding: "6vh 1vh",
    height: "100%",
    verticalAlign: "middle",
  },
  paper: {
    padding: theme.spacing(2),
    margin: "auto",
    maxWidth: 500,
    textAlign: "center"
  },
  avatar: {
    width: theme.spacing(15),
    height: theme.spacing(15),
  },
  avatarDiv: {
    alignItems: "center",
    display: "flex",
    justifyContent: "center",
    marginBottom: "2vh",
  },
  icon: {
    color: "#F58F6E",
  },
  chip: {
    backgroundColor: "#f7ac97",
    margin: "0.5vh 1vh 0.5vh 0",
  },
  chipList: {
    display: "flex",
    justifyContent: "left",
    flexWrap: "wrap",
    padding: "1vh 2vh 1vh 0",
  },
}));

export default Profile;
