import React, { useContext, useEffect, useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import { BrowserView, isBrowser, isMobile } from "react-device-detect";
import { Avatar, Paper, Typography } from "@material-ui/core";
import Post from "../post/Post";
import { auth, firestore } from "../../config/firebase";
import { UserContext } from "../../config/UserContext";

const mockPostData = [
  {
    owner: "Zezinho",
    date: "September 19, 2020",
    description: "OLHA só que legal!",
    image: require("../../assets/foto1.jpg"),
    tags: ["Projeto1", "Projeto2", "Projeto3", "Projeto4"],
    comments: [
      "comentario1",
      "comentario2",
      "comentario3",
      "comentario4",
      "comentario5",
    ],
  },
  {
    owner: "Selma",
    date: "September 1, 2020",
    description: "OLHA só que legal!",
    image: require("../../assets/foto3.jpg"),
    tags: ["Projeto1", "Projeto2"],
    comments: [
      "comentario1",
      "comentario2",
      "comentario3",
      "comentario4",
      "comentario5",
    ],
  },
  {
    owner: "Zezinho",
    date: "September 19, 2020",
    description: "OLHA só que legal!",
    image: require("../../assets/foto1.jpg"),
    tags: ["Projeto1", "Projeto2"],
    comments: [
      "comentario1",
      "comentario2",
      "comentario3",
      "comentario4",
      "comentario5",
    ],
  },
  {
    owner: "Selma",
    date: "September 1, 2020",
    description: "OLHA só que legal!",
    image: require("../../assets/foto3.jpg"),
    tags: ["Projeto1", "Projeto2"],
    comments: [
      "comentario1",
      "comentario2",
      "comentario3",
      "comentario4",
      "comentario5",
    ],
  },
];

const Home = () => {
  const currentUser = useContext(UserContext);
  const { uid, photoURL } = auth.currentUser;

  const [posts, setPosts] = useState([]);

  useEffect(() => {
    firestore
      .collection("post")
      .where("uid", "==", uid)
      .get()
      .then((snapshot) => {
        if (snapshot) {
          let data = [];
          snapshot.forEach((item) => data.push({ ...item.data() }));
          setPosts(data);
        }
      });
  }, [uid]);

  const classes = useStyles();
  return (
    <div className={classes.root}>
      <BrowserView style={{ flex: 2 }}>
        <div className={classes.profile}>
          <div className={classes.avatarDiv}>
            <Avatar
              alt="Remy Sharp"
              src={currentUser && currentUser.photoURL}
              className={classes.avatar}
            />
          </div>
          <Typography variant="h6" color="textSecondary" component="p">
            {currentUser && currentUser.displayName}
          </Typography>
        </div>
      </BrowserView>

      <div style={{ flex: 3 }}>
        <Paper className={classes.paper}>
          {posts.map((post, index) => {
            return (
              <div key={index} className={classes.postItem}>
                <Post
                  owner={mockPostData[0].owner}
                  date={new Date(post.date.seconds * 1000).toLocaleString()}
                  description={post.description}
                  image={post.photo}
                  project={post.project}
                  comments={mockPostData[0].comments}
                />
              </div>
            );
          })}
        </Paper>
      </div>
    </div>
  );
};

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    overflow: "hidden",
    padding: "6vh 1vh",
    height: "100%",
  },
  profile: {
    paddingTop: "150px",
    textAlign: "center",
    justifyContent: "center",
    flexDirection: "column",
  },
  paper: {
    padding: theme.spacing(2),
    margin: isMobile ? "auto": "",
    marginLeft: isMobile ? "": "10%",
    maxWidth: 500,
  },
  postItem: {
    marginBottom: "20px",
  },
  avatar: {
    width: theme.spacing(15),
    height: theme.spacing(15),
  },
  divBrowser: {
    flex: 2,
    textAlign: "center",
  },
  avatarDiv: {
    alignItems: "center",
    display: "flex",
    justifyContent: "center",
    marginBottom: "2vh",
  },
}));

export default Home;
